@import url("https://fonts.googleapis.com/css2?family=Outfit:ital,wght@0,400;1,200&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Outfit";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}
h2 {
  font-size: 3rem;
  padding-bottom: 10px;
}
h1 {
  font-size: 5rem;
}

.app {
  width: 100%;
  height: 25vh;
  position: relative;
  background-color: (0, 0, 0, 4);
  color: white;
}

.app::before {
  content: "";
  background: url("./assets/sebastian-banasiewcz-pERCUe02B4I-unsplash.jpg")
    no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
}
.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}
.container {
  max-width: 700px;
  height: 580px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  margin-bottom: 1rem;
  padding: 0.8rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 1);
  color: black;
}

.bottomFont {
  font-size: 1.2rem;
}
